// audioRecorder.js
import { ref, computed } from 'vue'
import { useAudioProcessor } from './audioProcessor'
import { checkAudioSupport } from './deviceChecker'

/**
 * WebSocket
 * @param {string} type -  ('audio'  'video')
 * @param {Object} callbacks - 
 * @param {ref} isRecordingRef - ref
 * @returns {Object} socketPromise
 */
export const initializeWebSocket = (type, callbacks, isRecordingRef) => {
    let socket
    try {
        const wsUrl =
            process.env.VUE_APP_WEBSOCKET_URL || 'ws://localhost:8080/websocket'
        socket = new WebSocket(wsUrl)
        socket.binaryType = 'arraybuffer'
    } catch (error) {
        console.error('WebSocket:', error)
        return Promise.reject(new Error('WebSocket'))
    }

    // WebSocket
    const connectPromise = new Promise((resolve, reject) => {
        const handleOpen = async () => {
            try {
                resolve(socket)
                socket.removeEventListener('error', tempErrorListener)
            } catch (e) {
                reject(e)
            }
        }

        // 
        const connectionTimeout = setTimeout(() => {
            reject(new Error('WebSocket'))
            if (socket && socket.readyState === WebSocket.OPEN) {
                try {
                    socket.close()
                    console.log('WebSocket')
                } catch (error) {
                    console.error('WebSocket:', error)
                }
            }
        }, 10000)

        const tempErrorListener = (error) => {
            console.error('WebSocket:', error)
            reject(new Error(''))
            clearTimeout(connectionTimeout)
            if (socket && socket.readyState === WebSocket.OPEN) {
                try {
                    socket.close()
                    console.log('WebSocket')
                } catch (error) {
                    console.error('WebSocket:', error)
                }
            } else {
                console.warn(
                    'WebSocket:',
                    socket?.readyState,
                )
            }
        }

        // 
        socket.addEventListener('open', handleOpen)
        socket.addEventListener('error', tempErrorListener)

        // 
        return () => {
            clearTimeout(connectionTimeout)
            socket.removeEventListener('open', handleOpen)
            socket.removeEventListener('error', tempErrorListener)
        }
    })

    // 
    const handleMessage = (event) => {
        if (event.data instanceof ArrayBuffer) {
            // 
            // 
            if (callbacks && callbacks.onMessage) {
                callbacks.onMessage(event)
            }
        }
    }

    // 
    const handleError = (error) => {
        if (callbacks && callbacks.onError) {
            callbacks.onError(error)
        }
    }

    // 
    const handleClose = (event) => {
        // 
        if (isRecordingRef && isRecordingRef.value && event.code !== 1000) {
            setTimeout(() => {
                // WebSocket
                const newConnection = initializeWebSocket(
                    type,
                    callbacks,
                    isRecordingRef,
                )
                // socket
                socket = newConnection.socket
            }, 5000) // 5
        }

        // 
        if (isRecordingRef) {
            isRecordingRef.value = false
        }
    }

    // 
    socket.addEventListener('message', handleMessage)
    socket.addEventListener('error', handleError)
    socket.addEventListener('close', handleClose)

    const cleanup = () => {
        socket.removeEventListener('message', handleMessage)
        socket.removeEventListener('error', handleError)
        socket.removeEventListener('close', handleClose)
        socket = null
    }

    return {
        socket,
        connectPromise,
        cleanup,
    }
}

export function useAudioRecorder() {
    const mediaRecorder = ref(null)
    const socketRef = ref(null)
    const audioContext = ref(null)
    const errorMessage = ref('')
    const isRecording = ref(false)
    const playbackQueue = ref([])
    const isUsingWorklet = ref(false)
    const playerNode = ref(null)
    let wsConnection = null

    const audioProcessor = useAudioProcessor({
        socket: socketRef,
        mediaRecorder,
        audioContext,
        playbackQueue,
        isUsingWorklet,
        playerNode,
        errorMessage,
    })

    // WebSocket
    const handleSocketMessage = (event) => {
        try {
            if (event.data instanceof ArrayBuffer) {
                audioProcessor.processAudioData(event.data)
            }
        } catch (error) {
            console.error(':', error)
            errorMessage.value = ''
        }
    }

    // 
    const handleError = (error) => {
        errorMessage.value = error.message || ''
        stopRecording()
    }

    // WebSocket
    const handleSocketMessageCallback = (event) => {
        handleSocketMessage(event)
    }

    // WebSocket
    const handleWebSocketError = (error) => {
        console.error('WebSocket')
        handleError(error)
    }

    // 
    async function startRecording() {
        try {
            console.log(':', isRecording.value)
            if (isRecording.value) {
                console.warn('')
                return
            }

            isRecording.value = true
            errorMessage.value = ''

            const wsUrl =
                process.env.VUE_APP_WEBSOCKET_URL ||
                'ws://localhost:8080/audio-stream'
            console.log('WebSocket: audio')
            // WebSocket
            wsConnection = initializeWebSocket(
                'audio',
                {
                    onMessage: handleSocketMessageCallback,
                    onError: handleWebSocketError,
                },
                isRecording,
                wsUrl,
            )

            console.log('WebSocket...')
            // WebSocket
            const socketInstance = await wsConnection.connectPromise
            socketRef.value = socketInstance

            console.log('WebSocket')
            const hasLocalAudio = await checkAudioSupport()
            console.log(':', hasLocalAudio)

            if (hasLocalAudio) {
                console.log('')
                await audioProcessor.startLocalRecording()
                // 
                const encoder = new TextEncoder()
                const audioModeMessage = encoder.encode(
                    JSON.stringify({ type: 'audio_mode', mode: 'local' }),
                )
                socketInstance.send(audioModeMessage.buffer)
            } else {
                // 
                socketInstance.send(
                    JSON.stringify({ type: 'audio_mode', mode: 'remote' }),
                )
                errorMessage.value = ''
                console.warn('')
            }
        } catch (error) {
            console.error(':', error)
            handleError(error)
            // WebSocket
            if (wsConnection && wsConnection.cleanup) {
                wsConnection.cleanup()
            }
        }
    }

    // 
    const stopRecording = () => {
        isRecording.value = false

        // 
        if (mediaRecorder.value) {
            mediaRecorder.value.stop()
            mediaRecorder.value.stream
                .getTracks()
                .forEach((track) => track.stop())
        }

        console.log('wsConnection:', wsConnection)

        // wsConnectionsocket
        if (wsConnection && wsConnection.socket) {
            console.log(
                'WebSocket:',
                wsConnection.socket.readyState,
            )
            // OPEN
            if (wsConnection.socket.readyState === WebSocket.OPEN) {
                try {
                    wsConnection.socket.close()
                    console.log('WebSocket')
                } catch (error) {
                    console.error('WebSocket:', error)
                }
            } else {
                console.warn(
                    'WebSocket:',
                    wsConnection.socket.readyState,
                )
            }
            // 
            if (wsConnection.cleanup) {
                wsConnection.cleanup()
                console.log('')
            }
            // wsConnectionnull
            wsConnection = null
        } else {
            console.warn('wsConnectionsocketnull')
        }
        // socketRefnull
        if (socketRef.value) {
            socketRef.value = null
        }

        // 
        isRecording.value = false
    }

    // API
    return {
        isRecording,
        errorMessage,
        startRecording,
        stopRecording,
        buttonStates: computed(() => ({
            startDisabled: isRecording.value,
            stopDisabled: !isRecording.value,
        })),
    }
}
